import { fetchGraphQl } from '../fetch-graphql';
import { handleNetworkError } from '../../lib/network-error';
import { handleFetchError } from '../../lib/fetch-error';
import { UpdateCompanyStructureInput } from '../../types/api/updateCompanyStructure.types';
import { UPDATE_COMPANY_STRUCTURE } from './graphql/updateCompanyStructure.graphql';

/**
 * Updates the company structure by moving a node to a new parent
 * 
 * @param input - Object containing the node ID and new parent ID
 * @returns Promise resolving to boolean indicating success
 * @throws {Error} When network errors or GraphQL errors occur
 */
export async function updateCompanyStructure(input: UpdateCompanyStructureInput): Promise<boolean> {
  // Transform camelCase input to GraphQL snake_case format
  const transformedInput = {
    treeId: input.id,
    parentTreeId: input.parentId,
  };

  return await fetchGraphQl<{ updateCompanyStructure?: { __typename: string } }>(UPDATE_COMPANY_STRUCTURE, {
    variables: transformedInput,
  })
    .then((res) => {
      if (res.errors?.length) return handleFetchError(res.errors);
      return !!res?.data?.updateCompanyStructure;
    })
    .catch(handleNetworkError);
}
