import { CompanyTeamModel } from '../models/company-team';

// GraphQL response interface (snake_case for direct GraphQL mapping)
interface CompanyTeamResponse {
  id: string;
  name: string;
  description?: string | null;
}

export const transformCompanyTeam = (response: CompanyTeamResponse): CompanyTeamModel => {
  // Validate response structure
  if (!response) {
    throw new Error('Invalid response: missing team data');
  }

  // Transform GraphQL snake_case response to camelCase model
  return {
    id: response.id,
    name: response.name,
    description: response.description,
    // Extended properties for UI and business logic
    // Note: memberCount should be calculated from structure data in components when needed
  };
};
