import { fetchGraphQl } from '../fetch-graphql';
import { handleNetworkError, handleFetchError } from '@/company-management/lib';
import { DELETE_COMPANY_TEAM } from './graphql/deleteCompanyTeam.graphql';

/**
 * Deletes a company team by their ID
 * 
 * @param id - The ID of the company team to delete (base64 encoded UID from GraphQL)
 * @returns Promise resolving to boolean indicating success
 * @throws {Error} When network errors or GraphQL errors occur
 */
export async function deleteCompanyTeam(id: string): Promise<boolean> {
  return await fetchGraphQl<{ deleteCompanyTeam?: { __typename: string } }>(DELETE_COMPANY_TEAM, { variables: { id } })
    .then((res) => {
      if (res.errors?.length) return handleFetchError(res.errors);
      return !!res?.data?.deleteCompanyTeam;
    })
    .catch(handleNetworkError);
}
