import type { CompanyStructureNode } from '../models/company-structure';

// Internal GraphQL response interface (snake_case for direct GraphQL mapping)
interface CompanyStructureResponse {
  items: CompanyStructureItemResponse[];
}

interface CompanyStructureItemResponse {
  id: string;
  parent_id: string | null;
  entity: {
    __typename: 'CompanyTeam' | 'Customer';
    companyTeamId?: string;
    customerId?: string;
    structure_id: string;
    firstname?: string;
    lastname?: string;
    status?: 'ACTIVE' | 'INACTIVE';
    job_title?: string | null;
    name?: string;
    description?: string | null;
  };
}

export function transformCompanyStructure(response: CompanyStructureResponse): CompanyStructureNode[] {
  const typed = response.items
    .filter((i) => {
      // Filter out inactive users (matching LUMA's filterTree behavior)
      // Teams are always active, so we only filter Customer entities
      if (i.entity.__typename === 'Customer' && 'status' in i.entity) {
        return i.entity.status === 'ACTIVE';
      }
      return true; // Keep all teams
    })
    .map((i) => ({
      structureId: i.entity.structure_id,
      parentStructureId: i.parent_id || null,
      label: i.entity.__typename === 'CompanyTeam' ? (i.entity.name || '') : `${i.entity.firstname || ''} ${i.entity.lastname || ''}`.trim(),
      type: i.entity.__typename === 'CompanyTeam' ? ('team' as const) : ('user' as const),
      entityId: (i.entity.__typename === 'CompanyTeam' ? i.entity.companyTeamId : i.entity.customerId) || '',
      description: i.entity.__typename === 'CompanyTeam' ? i.entity.description || null : (i.entity.job_title || null),
    }));

  const mapped: CompanyStructureNode[] = typed.map((t) => {
    const parentRaw = t.parentStructureId || null;
    const parentTyped = !parentRaw || parentRaw === 'MA==' ? null : parentRaw;
    return { id: t.structureId, parentId: parentTyped, label: t.label, type: t.type, entityId: t.entityId, description: t.description };
  });
  return mapped;
}
