import { fetchGraphQl } from '../fetch-graphql';
import { handleNetworkError } from '../../lib/network-error';
import { handleFetchError } from '../../lib/fetch-error';
import { CreateCompanyTeamInput, CreateCompanyTeamResult } from '../../types/api/createCompanyTeam.types';
import { CREATE_COMPANY_TEAM } from './graphql/createCompanyTeam.graphql';

/**
 * Creates a new company team with the provided information
 * 
 * @param input - Team information including name, description, and target location
 * @returns Promise resolving to created team result or null if creation failed
 * @throws {Error} When network errors or GraphQL errors occur
 */
export async function createCompanyTeam(input: CreateCompanyTeamInput): Promise<CreateCompanyTeamResult | null> {
  // Transform camelCase input to GraphQL snake_case format
  const transformedInput = {
    name: input.name,
    description: input.description,
    target_id: input.targetId,
  };

  return await fetchGraphQl<{ createCompanyTeam?: { team?: { id: string; structure_id?: string; name: string } } }>(CREATE_COMPANY_TEAM, { variables: { input: transformedInput } })
    .then((res) => {
      if (res.errors?.length) return handleFetchError(res.errors);
      const team = res?.data?.createCompanyTeam?.team;
      if (!team) return null;
      
      // Transform GraphQL snake_case response to camelCase
      return {
        id: team.id,
        structureId: team.structure_id,
        name: team.name,
      };
    })
    .catch(handleNetworkError);
}
