import { fetchGraphQl } from '../fetch-graphql';
import { handleNetworkError } from '../../lib/network-error';
import { handleFetchError } from '../../lib/fetch-error';
import { CompanyStructureNode } from '../../data/models';
import { transformCompanyStructure } from '../../data/transforms';
import { CompanyStructureResponse } from '../../types/api/getCompanyStructure.types';
import { GET_COMPANY_STRUCTURE } from './graphql/getCompanyStructure.graphql';

/**
 * Retrieves the company organizational structure as a tree of nodes
 * 
 * @returns Promise resolving to array of CompanyStructureNode representing the company hierarchy
 * @throws {Error} When network errors or GraphQL errors occur
 */
export async function getCompanyStructure(): Promise<CompanyStructureNode[]> {
  return await fetchGraphQl<{ company: { structure: CompanyStructureResponse } }>(GET_COMPANY_STRUCTURE, { method: 'GET', cache: 'no-cache' })
    .then((res) => {
      if (res.errors?.length) return handleFetchError(res.errors);
      const structure = res.data.company.structure;
      
      // Use transformer to convert GraphQL response to model
      return transformCompanyStructure(structure);
    })
    .catch(handleNetworkError);
}
