import { fetchGraphQl } from '../fetch-graphql';
import { handleNetworkError, handleFetchError } from '@/company-management/lib';
import { UpdateCompanyTeamInput } from '../../types/api/updateCompanyTeam.types';
import { UPDATE_COMPANY_TEAM } from './graphql/updateCompanyTeam.graphql';

/**
 * Updates an existing company team with the provided information
 * 
 * @param input - Team information including ID (base64 encoded UID) and fields to update
 * @returns Promise resolving to boolean indicating success
 * @throws {Error} When network errors or GraphQL errors occur
 */
export async function updateCompanyTeam(input: UpdateCompanyTeamInput): Promise<boolean> {
  // Transform camelCase input to GraphQL snake_case format
  const transformedInput = {
    id: input.id,
    name: input.name,
    description: input.description,
  };

  return await fetchGraphQl<{ updateCompanyTeam?: { team?: { id: string } } }>(UPDATE_COMPANY_TEAM, { variables: { input: transformedInput } })
    .then((res) => {
      if (res.errors?.length) return handleFetchError(res.errors);
      return !!res?.data?.updateCompanyTeam?.team?.id;
    })
    .catch(handleNetworkError);
}
