import { fetchGraphQl } from '../fetch-graphql';
import { handleNetworkError, handleFetchError } from '@/company-management/lib';
import { CompanyTeamModel } from '../../data/models';
import { transformCompanyTeam } from '../../data/transforms';
import { GET_COMPANY_TEAM } from './graphql/getCompanyTeam.graphql';

/**
 * Retrieves a specific company team by their ID
 * 
 * @param id - The ID of the company team to retrieve (base64 encoded UID from GraphQL)
 * @returns Promise resolving to CompanyTeamModel or null if team not found
 * @throws {Error} When network errors or GraphQL errors occur
 */
export async function getCompanyTeam(id: string): Promise<CompanyTeamModel | null> {
  return await fetchGraphQl<{ company: { team?: { id: string; name: string; description?: string | null } } }>(GET_COMPANY_TEAM, { variables: { id } })
    .then((res) => {
      if (res.errors?.length) return handleFetchError(res.errors);
      const team = res?.data?.company?.team;
      if (!team) return null;
      
      // Pure data transformation (no permissions)
      return transformCompanyTeam(team);
    })
    .catch(handleNetworkError);
}
